.article-content {
	.col_lg-left {
		padding-right: 40px;
		@include md {
			padding-right: 0; } }
	.article-table {
		padding-bottom: 30px; }

	.accompanying-product {
		padding-top: 36px;
		padding-bottom: 42px;
		@include md {
			display: none; } }
	.article-faq {
		padding-top: 35px;
		padding-bottom: 35px; }

	.similar-post {
		padding-top: 47px;
		padding-bottom: 99px;
		@include sm {
			padding: 0;
			.rel-posts {
				padding-top: 0; } } }
	.article-review {
		margin-bottom: 45px; }
	.article-reviews {
		@include md {
			display: none; } }
	.article-rec {
		margin-bottom: 26px;
		@include md {
			display: none; } }
	.article-info {
		margin-bottom: 45px;
		@include md {
			display: none; } } }
