.burger {
  position: relative;
  height: 17px;
  width: 22px;
  background-color: transparent;
}

.burger:before, .burger:after, .burger i {
  width: 100%;
  position: absolute;
  left: 0;
  height: 3px;
  background-color: #1a1a1a;
  transition: .25s ease;
  border-radius: 50px;
}

