  .burger:before, .burger:after, .burger i {
    background-color: #14c1e5;
  }
}

.burger:before, .burger:after {
  content: '';
  transform: rotate(0) scaleX(1);
}

.burger:before {
  top: 0;
  transform-origin: top right;
}

.burger i {
  top: 7px;
  transform: scale(1);
  opacity: 1;
}

.burger:after {
  bottom: 0;
  transform-origin: bottom right;
}

.burger.is-active:before {
  transform: rotate(-39deg) scaleX(1.05);
}

.burger.is-active:after {
  transform: rotate(39deg) scaleX(1.05);
}

.burger.is-active i {
  transform: scale(0.2);
  opacity: 0;
}

